<!--  -->
<template>
  <div>
        <Zi @bianji="address" bool1="true"  goback="true" data="<" con="编辑地址"  :main=val>
        <h1 @click="$router.go(-1)" slot="left">
        <
      </h1>
      </Zi>
      <div class="main">
  <ul>
        <li @click="orderIndex(index)" style="position:relative" v-for="(item,index) of arr" :key="index">
           <span  style="position:absolute;right:0.3rem;top:0.2rem;display:block;line-height:0.5rem" @click="del(index)" v-show="bool">x</span>
          <p>{{item.name}}</p>
          <p>{{item.phone}}</p>
         
        </li>
      </ul>
        <router-link to="/address/addaddress" tag="div" class="di">
         新增地址
          <span>></span>
        </router-link>
      </div>
      
    
  </div>
</template>

<script>
import Zi from "../../../components/header"
export default {
  data () {
    return {
        arr:[],
        val:"完成",
        bool:false
    };
  },

  components: {Zi},

  computed: {},
beforeMount(){
  this.arr=this.$store.state.arr
    console.log(this.arr)
},
  mounted(){



  },

  created(){},

  methods: {
      address(){
           if(this.val=="完成"){
             this.bool=true
             this.val="编辑"
           }else{
               this.bool=false
               this.val="完成"
           }
         },
         del(index){
           this.$store.commit('xiu',index)
         },
         orderIndex(index){
           this.$router.push({path:'/OrderPay',query:{index:index}})
         }
      }
}

</script>
<style scoped lang="scss">
.di{
    margin-top: 0.2rem;
    line-height: 0.7rem;
    background: white;
    padding-left: 0.2rem;
    span{
        float: right;
        margin-right: 0.3rem;
        color: gainsboro;
    }
}
.main{
 margin-top: 1.2rem;
 ul{
  li{
    height: 1rem;
    background: white;
    margin-bottom: 1px;
    line-height: 1rem;
    p{
      line-height: 0.5rem ;margin-left: 0.15rem;   }
  }
}
}

</style>


